function CartV1() {
  this.cart = [];
  this.currentId = 0;
}

// prototype ÏÜçÏÑ±Ïóê getNewIdÌï®ÏàòÎ•º ÎÑ£Í≥† ÏûàÎã§. prototype ÏÜçÏÑ±Ïóê ÎÑ£ÏùÑ Í≤ΩÏö∞ Ïù∏Ïä§ÌÑ¥Ïä§ Í∞ùÏ≤¥Ïóê Îì±Ïû•ÌïòÍ≤å Îê† Î©îÏÜåÎìúÎùºÍ≥† ÏÉùÍ∞ÅÌïòÎ©¥ ÎêúÎã§.
CartV1.prototype.getNewId = function () {
  this.currentId++;
  return this.currentId;
};

// createItemÏùÄ CartV1 ÏûêÏ≤¥Ïóê Ìï®ÏàòÎ•º ÎÑ£Í≥† ÏûàÎã§. Ïù∏Ïä§ÌÑ¥Ïä§ Í∞ùÏ≤¥ÏóêÎäî ÎìúÎü¨ÎÇòÏßÄ ÏïäÏäµÎãàÎã§.
CartV1.createItem = function (name, price) {
  return {
    name,
    price,
  };
};

CartV1.prototype.addItem = function (item) {
  this.cart.push({
    ...item,
    id: this.getNewId(),
  });
};

CartV1.prototype.clearCart = function (item) {
  this.cart = [];
  this.currentId = 0;
};

const shoppingCartV1 = new CartV1();

shoppingCartV1.addItem(CartV1.createItem("ÏàòÎ∞ï", 8000));
shoppingCartV1.addItem(CartV1.createItem("ÏÇ¨Í≥º", 12000));
shoppingCartV1.addItem(CartV1.createItem("ÎëêÎ∂Ä", 2000));

console.log(shoppingCartV1.cart);

class CartV2 {
  // static Ïù∏Ïä§ÌÑ¥Ïä§ Í∞ùÏ≤¥ÏóêÎäî ÎìúÎùºÎÇòÏßÄ ÏïäÍ≥† classÏûêÏ≤¥Ïóê Î∂ôÏñ¥ ÏûàÎäî ÏÜçÏÑ±.
  static createItem = (name, price) => ({
    name,
    price,
  });

  cart;
  currentId;

  constructor() {
    this.currentId = 0;
    this.cart = [];
  }

  getNewId = () => {
    this.currentId++;
    return this.currentId;
  };

  addItem = (item) => {
    this.cart.push({
      ...item,
      id: this.getNewId(),
    });
  };

  clearCart = () => {
    this.currentId = 0;
    this.cart = [];
  };
}

const shoppingCartV2 = new CartV2();

shoppingCartV2.addItem(CartV2.createItem("ÏàòÎ∞ï", 8000));
shoppingCartV2.addItem(CartV2.createItem("ÏÇ¨Í≥º", 12000));
shoppingCartV2.addItem(CartV2.createItem("ÎëêÎ∂Ä", 2000));

console.log(shoppingCartV2.cart);
